import React, { useEffect, useState } from "react";
import { Navbar, Container, Nav, NavDropdown } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "../utils/style/Bootstrap.scss";
import data from "../utils/data/data";
import { Link, Route, Switch, useHistory, useParams } from "react-router-dom";
import axios from "axios";

const Bootstrap = () => {
    const [shoes, setShoes] = useState(data);

    return (
        <div>
            <Navbar bg="light" expand="lg">
                <Container>
                    <Navbar.Brand href="#home">TIGER KING SHOES</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">
                            <Nav.Link>
                                {" "}
                                <Link to="/">Home</Link>
                            </Nav.Link>
                            <Nav.Link>
                                <Link to="/detail">Detail</Link>
                            </Nav.Link>
                            <NavDropdown
                                title="Dropdown"
                                id="basic-nav-dropdown"
                            >
                                <NavDropdown.Item href="#action/3.1">
                                    Action
                                </NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.2">
                                    Another action
                                </NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.3">
                                    Something
                                </NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#action/3.4">
                                    Separated link
                                </NavDropdown.Item>
                            </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

            <Route exact path="/">
                <div className="jumbotron">
                    <h2>20% now on sale</h2>
                    <p>
                        often more interesting or more meaningful than the
                        verbal content of the text. ... fluctuate between word
                        and image, fluid forms do not become meaningless. ...
                        vice versa,” or from “the verbal code (the word) to the
                        visual code (the image).
                    </p>
                </div>

                <div className="container">
                    <div className="row">
                        {shoes.map((shoe) => (
                            <Details shoe={shoe} />
                        ))}
                    </div>
                </div>

                <button
                    className="btn btn-primary"
                    onClick={() => {
                        axios
                            .get(
                                "https://codingapple1.github.io/shop/data2.json"
                            )
                            .then((res) => {
                                setShoes([...shoes, ...res.data]);
                            });
                    }}
                >
                    More
                </button>
            </Route>

            <Route path="/detail/:id">
                <DetailPage shoes={shoes} />
            </Route>
        </div>
    );
};

const Details = ({ shoe }) => {
    const { id, img, title, content, price } = shoe;
    return (
        <div className="col-md-4">
            <img src={img} width="100%"></img>
            <h4>{title}</h4>
            <p>
                {content}, ${price}
            </p>
        </div>
    );
};

const DetailPage = ({ shoes }) => {
    let { id } = useParams();
    let history = useHistory();
    const [active, setActive] = useState(true);
    let [input, setInput] = useState("");

    let product = shoes.filter((item) => item.id == id)[0]; // Its been hard!!!!!!!!
    // let product = shoes.find(item => item.id == id) // this one is another answer .

    console.log(product);

    useEffect(() => {
        let count = setTimeout(() => {
            setActive(false);
        }, 2000);
        return () => {
            clearTimeout(count);
        };
    }, [alert]);

    return (
        <div className="container">
            <input onChange={(e) => setInput(e.target.value)} />
            <p>{input}</p>

            {active ? (
                <div className="alert">
                    <p>Stock going fast!! hurry up to get that discount!</p>
                </div>
            ) : null}

            <div className="row">
                <div className="col-md-6">
                    <img src={product.img} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{product.title}</h4>
                    <p>{product.content}</p>
                    <p>{product.price}</p>
                    <button className="btn btn-danger">주문하기</button>
                    <button
                        className="btn btn-danger"
                        onClick={() => {
                            history.goBack();
                        }}
                    >
                        뒤로가기
                    </button>
                </div>
            </div>
        </div>
    );
};

export default Bootstrap;
