import React, {useState} from 'react';
import logo from '../logo.svg';
import '../App.css'

function App() {
  let [subject] = useState (['Jason Bday', 'Luth Bday', 'Charles Bday'])
  let [dates] = useState (['12th Dec', '13th Nov', '6th Oct'])
  let [modal, modalEdit] = useState(true)
  let [page, pageEdit] = useState(0)
  let [likes, likesEdit] = useState([0,0,0])
  
  return(
    <div className = 'App'>
    <div className='black-nav'>
      Daily Blog
    </div>

    {subject.map(function(a, i){
      return(

        <div className = 'list'>
          <h3 onClick = { () => { pageEdit(i) } }>{a} <span onClick = { ()=>{ likesEdit((likes[i])+1) } }> ❤️ {likes[i]} </span> </h3>
          <p>{dates[i]}</p>
          <hr/>
        </div>

      )     
    })
    }

<button onClick = { () => {modalEdit(!modal)} }>open / close</button>

{
  modal === true
  ? <Modal subject = {subject} page = {page} dates = {dates}></Modal> //props
  : null
}


    </div>
  )
}

function Modal(props){
  return(
    <div className = 'modal'>
      <h3> {props.subject[props.page]} </h3>
      <p>{props.dates[props.page]}</p>
      <p>established name</p>
    </div>
  )
}

export default App;
